class UI {
  constructor(user) {
    this._user = user;
    this._Init();
  }

  _Init() {
    this._loginBtn = document.getElementById('login-btn');
    this._accountPlace = document.getElementById('account-place');
  }

  AccounLoggedIn(user, userInfo) {
    this._loginBtn.style.display = 'none';
    this._accountPlace.style.display = 'block';
    this._accountPlace.innerHTML = `
		<div class="account-bubble">
			<i class="fa-solid fa-user"></i>
			<div class="account-info" id="account-info">
				<div class="account-image">
					<i class="fa-solid fa-user"></i>
				</div>
				<h3>${userInfo.username}</h3>
				<p>${userInfo.email}</p>
				<button class="button grey" id="account-btn-settings">
					Setting
				</button>
				<button class="button account-btn-logout">
					Log out
				</button>
			</div>
		</div>`;
  }

  AccounLoggedOut() {
    this._loginBtn.style.display = 'block';
    this._accountPlace.style.display = 'none';
  }

  ShowNotification(text, type) {
    let notifyElement = document.getElementById('notification');

    if (notifyElement.style.display !== 'block') {
      notifyElement.style.display = 'block';
      notifyElement.innerHTML = `<p>${text}</p>`;
      if (type == 'success-ntf') {
        notifyElement.classList.add('success-ntf');
      }
      if (type == 'warning-ntf') {
        notifyElement.classList.add('warning-ntf');
      }
      if (type == 'error-ntf') {
        notifyElement.classList.add('error-ntf');
      }
      setTimeout(() => {
        notifyElement.style.display = 'none';
        if (type == 'success-ntf') {
          notifyElement.classList.remove('success-ntf');
        }
        if (type == 'warning-ntf') {
          notifyElement.classList.remove('warning-ntf');
        }
        if (type == 'error-ntf') {
          notifyElement.classList.remove('error-ntf');
        }
      }, 3000);
    }
  }
}

export default UI;
